    min-height: calc(100vh - 160px);
}
.row_nav {
    display: flex;
    flex: 1;
    border-radius: 0px 4px 4px 0px;
    border: 1px solid #e7e7e7;
    padding: 20px;
    .left_nav {
        width: 20%;
        min-height: 500px;
        margin-right: 24px;
        border-radius: 0px 4px 4px 0px;
        border: 1px solid #e7e7e7;
        .left-title {
            display: flex;
            padding: 18px 24px 18px 24px;
            justify-content: space-between;
            align-items: center;
            .title_text {
                font-size: 14px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: #999999;
                line-height: 20px;
            }
            .title-icon {
                font-size: 18px;
                .icon-box {
                    cursor: pointer;
                    display: inline-block;
                    padding: 6px;
                    &:hover {
                        opacity: 0.8;
                    }
                }
            }
        }
    }
}

.ul-list ul li {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0 24px;
    height: 44px;
    line-height: 44px;
    cursor: pointer;
    i {
        font-size: 16px;
        padding: 4px 8px;
    }
}
.ul-list ul li.active {
    background: #fff5f2;
    font-weight: bold;
    border-left: 4px solid #fb4702;
}
